import { NgModule }       from '@angular/core';
import { BrowserModule }  from '@angular/platform-browser';
import { FormsModule }    from '@angular/forms';
import { HttpClientModule }    from '@angular/common/http';

import { HttpClientInMemoryWebApiModule } from 'angular-in-memory-web-api';
import { InMemoryDataService }  from './in-memory-data.service';

import { AppRoutingModule }     from './app-routing.module';

import { AppComponent }         from './app.component';





import {HomeComponent} from './components/home/home.component';
import {ProsDetailComponent} from './components/pros-detail/pros-detail.component';
import {ConsDetailComponent} from './components/cons-detail/cons-detail.component';
import {ProsSvc} from './services/ProsSvc';
import {ConsSvc} from './services/ConsSvc';









@NgModule({
  imports: [
    BrowserModule,
    FormsModule,
    AppRoutingModule,
    HttpClientModule,

    // The HttpClientInMemoryWebApiModule module intercepts HTTP requests
    // and returns simulated server responses.
    // Remove it when a real server is ready to receive requests.
    HttpClientInMemoryWebApiModule.forRoot(
      InMemoryDataService, { dataEncapsulation: false }
    )
  ],
  declarations: [
    AppComponent,
   
    HomeComponent,
    ProsDetailComponent,
    ConsDetailComponent,
   
  ],
  providers: [ProsSvc ,ConsSvc ],
  bootstrap: [ AppComponent ]
})
export class AppModule { }



